      blog: 'Janus Blog',
      aboutMe: 'I am an junior at Lafayette College majoring Computer Science and minoring in Math. I am interested in web development, machine learning, AI, and game programming. Currently seeking for 2019 summer Intern.',
      experience: [
        {
          position: 'Peer Tutor',
          company: 'Lafayette College | Easton, PA',
          description: '- Provide tutoring in data structure and algorithm course to college students. \n - Help solving coding problems in lab assignments and projects, explaining the algorithms and reviewing the codes for students.',
          startDate: 'Sept 2017',
          endDate: 'Present'
        },
        {
          position: 'Backend Developer Intern',
          company: 'Beavers Tech | Shenzhen, China',
          description: '- Studied vertx framework and applied in file server.\n- Assigned to collect data, including running distance, shooting average, hit ratio, etc, of soccer players to further analyze and evaluate their performance.',
          startDate: 'July 2017',
          endDate: 'August 2017'
        }
      ],
      projects: [
        {
          position: 'Banking Application System',
          language: 'Python',
          description: '- Implementing online banking application system which allows users to check bank account overview, statement. \n- Building database with PostgreSQL to store user’s bank accounts information. \n- Using Django framework to construct back-end and designing web UI with React.',
          startDate: 'Sept 2018',
          endDate: 'Present'
        },
        {
          position: 'Kaggle Machine Learning Competition - Titanic',
          language: 'Python',
          description: '- Applied machine learning to analyze and predict, in 418 passengers, what kinds of people were likely to survive in a Titanic shipwreck based on a train set of 890 passengers’ information. \n- Visualized passengers’ information correlations. Cleaned data by stripping and unifying passenger’s titles, filling in the missing age data by merging Linear Regressor, Gradient Boosting Regressor and Random Forest Regressor, and evening out outliers in ticket fares.\n- Fine-tuned the model by deleting useless data and obtained an accuracy from 68% to 77% on the test set by changing the model from Random Forest Classifier to Voting Classifier with Random Forest Classifier, Gradient Boosting Classifier and Extra Tree Classifier.',
          startDate: 'July 2017',
          endDate: 'August 2017'
        },
        {
          position: 'Emergency Response and Awareness System',
          language: 'C++',
          description: '- Developed an emergency response and awareness system with data gathering, storing and analyzing features.\n- Implemented three GUIs for three different end users including first responders, emergency planner and citizens \n - Implemented walkie-talkie, mapping and patrolling features for first responders; emergency simulator and command center for emergency planner; news feeding and reporting for citizens. \n - Used MVC pattern as whole system architecture and SQLite to build database for data storing and processing. \n - Used google map API for mapping feature and Git for source code management.',
          startDate: 'Feb 2018',
          endDate: 'May 2018'
        },
        {
          position: 'Distance Vector Simulator',
          language: 'Java',
          description: '- Simulated distance vector algorithm with multi-threaded and network programming. \n-  Enabled the simulated router to update its distance vector and forwarding table whenever it receives vector; pass messages to assigned IP address based on forwarding table data; support poisoned reverse.\n - Implemented distance vector algorithm based on Bellman-Ford method to implement the update process. \n- Tested the simulator with five groups of simulated routers; each group has over ten routers.',
          startDate: 'May 2018',
          endDate: 'May 2018'
        }
      ]
    }
  },
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      // our custom jQuery code goes here
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })
      // Closes responsive menu when a scroll trigger link is clicked
      $('.js-scroll-trigger').click(function () {
